// Docs

.ld-example {
	background-color: #fff;
	border: 1px solid #ddd;
	margin-bottom: 15px;
	padding: 15px;

	.table {
		td {
			padding: 0;
			border: none;
			border-bottom: 1px solid #ddd;
		}
		tr {
			&:last-child td {
				border-bottom: none;
			}
		}
		.type-info {
			color: #767676;
			vertical-align: middle;
		}

	}

	> .highlight, > .zero-clipboard+.highlight {
		margin: 0 -15px -15px;
		border-width: 1px;
		border-bottom-right-radius: 4px;
		border-bottom-left-radius: 4px;
		padding: 9px 14px;
		background-color: #f7f7f9;
		border-top: 1px solid #e1e1e8;
		border-bottom: 0;

		pre {
			padding: 0;
			margin-top: 0;
			margin-bottom: 0;
			word-break: normal;
			white-space: nowrap;
			background-color: transparent;
			border: 0;

			code {
				padding: 0;
				white-space: pre-wrap;
				background-color: transparent;
				border-radius: 0;
				font-size: inherit;
				  color: #333;
				  
				  &:first-child {
					display: inline-block;
					padding-right: 45px;
				}
			}
		}
	}
	.nt {
		color: #2f6f9f;
	}
	.s {
		color: #d44950;
	}
	.na {
		color: #4f9fcf;
	}
}

code {
    padding: 2px 4px;
    font-size: 90%;
    color: #c7254e;
    background-color: #f9f2f4;
    border-radius: 4px;
}
sidebar {
	&.highlight {
		ul {
			margin-top: 7px;
			li {
				margin-bottom: 7px;
			}

			ul {
				padding-left: 15px;
			}
		}
	}
}